import { JShape, JCircle, JLine, JPetal } from "../ui/JShapes";
import { Circle, Line, Petal, Cardioid, Limacon } from "../ast/Shapes";
import { Rotation, Repetition, Overlay } from "../ast/Transformations";
import {
  Article,
  ArticleAssignment,
  NumAssignment,
  Draw,
  Program,
  Statement,
} from "../ast/Program";
import { ArticleVariable, NumberVariable } from "../ast/Variables";
import { Visitor } from "../ast/Visitor";
import {
  degToRad,
  evaluateAE,
  evaluateComparison,
  greaterThanZero,
} from "../utils/math_utils";
import { ArithExp, CompExp } from "../ast/Expressions";
import { ForLoop, IfBlock } from "../ast/ControlBlocks";
import { Num } from "../ast/Num";

// Constructs JShapes from the given ast objects
// creates jshapes to be independent from one and other

export class Interpreter implements Visitor<JShape[], number, boolean> {
  // maps string keys to lists of jtype Shape values
  private shapeDict: { [varNames: string]: JShape[] };
  // maps string keys to numbers
  private numVarDict: { [varNames: string]: number };

  constructor() {
    this.shapeDict = {};
    this.numVarDict = {};
  }

  // Iterates throught the statements and fills in
  // the dictionaries and returns a list of JShapes
  // to be drawn
  visitProgram(program: Program): JShape[] {
    const statements: Statement[] = program.statements;
    for (const s of statements) {
      s.accept(this);
    }

    const toDraw: Draw = program.draw;
    return toDraw.accept(this);
  }

  // Fills the shape dictionary with the interpreted shape
  // that is assigned with the name varName
  visitArticleAssignment(assignment: ArticleAssignment): void {
    const varName: string = assignment.variable;
    const shape: Article = assignment.value;
    const interpShape: JShape[] = shape.accept(this);
    this.shapeDict[varName] = interpShape;
  }

  // Fills the number variable dictionary with the evaluated
  // number that is assigned with the name varName
  visitNumAssignment(assignment: NumAssignment): void {
    const varName: string = assignment.variable;
    const value: number = assignment.value.accept(this);
    this.numVarDict[varName] = value;
  }

  // Returns a list of Jshapes to be drawn
  visitDraw(draw: Draw): JShape[] {
    const toDraw: Article = draw.draw;
    return toDraw.accept(this);
  }

  // Iterates through the for loop object with counter (loop variable)
  // between the values start and end-1. Makes the
  // variable accessible in the loop and updates its value,
  // visits each for loop statement
  visitForLoop(loop: ForLoop): void {
    const counter: string = loop.counter;
    const start: number = loop.rangeStart.accept(this);
    const end: number = loop.rangeEnd.accept(this);

    for (let i = start; i < end; i++) {
      this.numVarDict[counter] = i;
      for (const s of loop.statements) {
        s.accept(this);
      }
    }

    // counter variable is only available inside for loop
    delete this.numVarDict[counter];
  }

  // Interprets the ifblock object by checking
  // its condition, if it holds then iterates through
  // its then statements, if not and if it has an else
  // case iterates throught the else statements
  visitIfBlock(ifBlock: IfBlock): void {
    const condition: boolean = ifBlock.condition.accept(this);
    const ifStatements: Statement[] = ifBlock.ifStatements;
    const elseStatements: Statement[] | undefined = ifBlock.elseStatements;

    if (condition) {
      ifStatements.forEach((statement) => {
        statement.accept(this);
      });
    } else if (elseStatements !== undefined) {
      elseStatements.forEach((statement) => {
        statement.accept(this);
      });
    }
  }

  // Returns the evaluated arithmetic expression
  visitArithExp(arithExp: ArithExp): number {
    const leftVal: number = arithExp.value.accept(this);
    if (arithExp.arithOp !== undefined && arithExp.otherExp !== undefined) {
      const rightVal: number = arithExp.otherExp.accept(this);
      return evaluateAE(leftVal, rightVal, arithExp.arithOp);
    } else {
      return leftVal;
    }
  }

  // Returns the evaluated comparison expressions
  visitCompExp(compExp: CompExp): boolean {
    const leftVal: number = compExp.leftArithExp.accept(this);
    const rightVal: number = compExp.rightArithExp.accept(this);
    return evaluateComparison(leftVal, rightVal, compExp.compOp);
  }

  // Gets the shapes and updates their angles with
  // the rotation angle (in radians) returns the updated
  // shapes
  visitRotation(rotation: Rotation): JShape[] {
    const shapes: JShape[] = rotation.shape.accept(this);
    const angle: number = degToRad(rotation.angle.accept(this));
    for (const s of shapes) {
      s.rotate(angle);
    }
    return shapes;
  }

  // Gets the shapes and repeats the shape reps
  // times at the offset while also rotating them in
  // s.repeat. Returns the updated shapes
  visitRepetition(repetition: Repetition): JShape[] {
    const shapes: JShape[] = repetition.shape.accept(this);
    let reps: number = repetition.repetitions.accept(this);
    const offset: number = repetition.offset.accept(this);

    // use a whole number of repetitions
    reps = Math.round(reps);
    greaterThanZero(reps, "of repetitions in call to repeat");

    const repeatedShapes: JShape[] = [];
    for (const s of shapes) {
      const rss: JShape[] = s.repeat(reps, offset);
      repeatedShapes.push(...rss);
    }
    return repeatedShapes;
  }

  // Gets the shapes from the overlay object
  // and combines each shape's included shapes in
  // single array. Returns the new array
  visitOverlay(overlay: Overlay): JShape[] {
    const articles: Article[] = overlay.shapes;
    const shapes: JShape[] = [];
    for (const a of articles) {
      shapes.push(...a.accept(this));
    }
    return shapes;
  }

  // Returns the list of copied shapes for the variable varName
  visitArticleVariable(variable: ArticleVariable): JShape[] {
    const varName: string = variable.name;
    return this.shapeDict[varName].map((shape) => shape.copy());
  }

  // Returns the number value of varName
  visitNumberVariable(variable: NumberVariable): number {
    const varName: string = variable.name;
    return this.numVarDict[varName];
  }

  // Gets the number value of the object Num
  visitNum(number: Num): number {
    return number.value;
  }

  // Creates and returns a new circle with the radius, color
  // and mode throws an error if radius of the circle is not
  // valid
  visitCircle(circle: Circle): JShape[] {
    const radius: number = circle.radius.accept(this);
    greaterThanZero(radius, "radius of circle");

    return [
      new JCircle(
        {
          x: 0,
          y: 0,
        },
        circle.radius.accept(this),
        circle.color,
        circle.mode,
      ),
    ];
  }

  // Creates and returns a new line with the length
  // and color throws an error if the length is not valid
  visitLine(line: Line): JShape[] {
    const length: number = line.size.accept(this);
    greaterThanZero(length, "length of line");

    return [
      new JLine(
        [
          { x: 0, y: 0 },
          { x: length, y: 0 },
        ],
        line.color,
      ),
    ];
  }

  // Creates and returns a new petal with the size, color
  // and mode throws an error if size is not valid
  visitPetal(petal: Petal): JShape[] {
    const size: number = petal.size.accept(this);
    greaterThanZero(size, "size of petal");

    return [
      new JPetal("petal", { x: 0, y: 0 }, 0, size, petal.color, petal.mode),
    ];
  }

  // Creates and returns a new cardioid with the size, color
  // and mode throws an error if size is not valid
  visitCardioid(cardioid: Cardioid): JShape[] {
    const size: number = cardioid.size.accept(this);
    greaterThanZero(size, "size of cardioid");

    return [
      new JPetal(
        "cardioid",
        { x: 0, y: 0 },
        0,
        size,
        cardioid.color,
        cardioid.mode,
      ),
    ];
  }

  // Creates and returns a new limacon with the size, color
  // and mode throws an error if size is not valid
  visitLimacon(limacon: Limacon): JShape[] {
    const size: number = limacon.size.accept(this);
    greaterThanZero(size, "size of limacon");

    return [
      new JPetal(
        "limacon",
        { x: 0, y: 0 },
        0,
        size,
        limacon.color,
        limacon.mode,
      ),
    ];
  }
}
