import { CharStreams, CommonTokenStream } from "antlr4ts";
import { MandalaLexer } from "./antlr/MandalaLexer";
import { MandalaParser } from "./antlr/MandalaParser";
import { ParseTreeToAST } from "./ParseTreeToAST";
import { Program } from "../ast/Program";

export class Main {
  // Gets the code string and returns the program ast
  public static parseCode(code: string): Program {
    // Create the Lexer
    const inputStream = CharStreams.fromString(code);
    const lexer = new MandalaLexer(inputStream);
    /*PRINT STATEMENTS - FOR TESTING ONLY
    lexer.getAllTokens().forEach((token) => {
      // eslint-disable-next-line no-console
      console.info(token);
    });
    */
    lexer.reset();
    const tokenStream = new CommonTokenStream(lexer);
    tokenStream.fill();

    // Create the Parser
    const parser = new MandalaParser(tokenStream);

    const visitor: ParseTreeToAST = new ParseTreeToAST();

    // Visit the tree
    const parsedProgram: Program = visitor.visitProgram(parser.program());
    /* PRINT PROGRAM OBJECT - FOR TESTING ONLY
    // Returned program:
    // eslint-disable-next-line no-console
    console.info(parsedProgram);
    */
    return parsedProgram;
  }
}
