import React, { useState } from "react";
import { polarToCartesian } from "../../utils/math_utils";
import {
  Point,
  Circle as ICircle,
  Line as ILine,
  Petal as IPetal,
  Shape as TShape,
} from "../types";

interface GridLinesProps {
  axisLength: number;
}

function GridLines(props: GridLinesProps) {
  const { axisLength } = props;
  const lines: JSX.Element[] = [];

  const styles: { [name: string]: React.CSSProperties } = {
    minor: {
      stroke: "lightGrey",
      strokeWidth: 0.03,
    },
    major: {
      stroke: "lightGrey",
      strokeWidth: 0.06,
    },
    axis: {
      stroke: "grey",
      strokeWidth: 0.09,
    },
  };

  // create the horizontal lines
  for (let i = -axisLength + 1; i < axisLength; i++) {
    lines.push(
      <line
        key={`y${i}`}
        x1={-axisLength}
        y1={i}
        x2={axisLength}
        y2={i}
        style={i % 5 === 0 ? styles.major : styles.minor}
      />,
    );
  }

  // create the vertical lines
  for (let i = -axisLength + 1; i < axisLength; i++) {
    lines.push(
      <line
        key={`x${i}`}
        x1={i}
        y1={-axisLength}
        x2={i}
        y2={axisLength}
        style={i % 5 === 0 ? styles.major : styles.minor}
      />,
    );
  }

  // create the x-axis line
  lines.push(
    <line
      key="xAxis"
      x1={0}
      y1={-axisLength}
      x2={0}
      y2={axisLength}
      style={styles.axis}
    />,
  );

  // create the y-axis line
  lines.push(
    <line
      key="yAxis"
      x1={-axisLength}
      y1={0}
      x2={axisLength}
      y2={0}
      style={styles.axis}
    />,
  );

  return <>{lines}</>;
}

interface ErrorMessageProps {
  error: Error | null;
}

function ErrorMessage(props: ErrorMessageProps) {
  const { error } = props;

  const style: React.CSSProperties = {
    fill: "red",
    fontSize: 1,
    fontFamily: "Courier New",
  };

  if (error === null) {
    return null;
  }

  const getWrappedMessage = (msg: string, lineLength: number) => {
    const words = msg.split(" ");
    const lines: string[] = [];

    words.forEach((word) => {
      if (lines.length === 0) {
        lines.push(word);
        return;
      }

      const lastLine = lines[lines.length - 1];

      if (lastLine.length + word.length + 1 > lineLength) {
        lines.push(word);
      } else {
        lines[lines.length - 1] = `${lastLine} ${word}`;
      }
    });

    return lines;
  };

  const startingPoint = { x: -18, y: -18 };
  const lineSpacing = 1.5;
  const lines = getWrappedMessage(`Error: ${error.message}`, 59);

  return (
    <>
      {lines.map((line, idx) => (
        <text
          key={idx}
          x={startingPoint.x}
          y={startingPoint.y + lineSpacing * idx}
          style={style}
        >
          {line}
        </text>
      ))}
    </>
  );
}

interface CardioidProps {
  cardioid: IPetal;
}

function Cardioid(props: CardioidProps) {
  const { center, angle, size, color, mode } = props.cardioid;

  const getInterpolatedPoints = (steps: number) => {
    const points: Point[] = [];

    for (let theta = 0; theta <= 2 * Math.PI; theta += (2 * Math.PI) / steps) {
      const alpha = size / 2;
      const r = alpha + alpha * Math.cos(theta - angle);

      let { x, y } = polarToCartesian(r, theta);
      x += center.x;
      y += center.y;

      points.push({ x, y: -y });
    }

    return points.map(({ x, y }) => `${x} ${y}`).join(",");
  };

  const style: React.CSSProperties = {
    fill: color,
    fillOpacity: mode === "solid" ? 1 : 0,
    stroke: color,
    strokeOpacity: mode === "outline" ? 1 : 0,
    strokeWidth: mode === "outline" ? 0.2 : 0,
  };

  return <polyline points={getInterpolatedPoints(360)} style={style} />;
}

interface CircleProps {
  circle: ICircle;
}

function Circle(props: CircleProps) {
  const { center, radius, color, mode } = props.circle;

  const style: React.CSSProperties = {
    fill: color,
    fillOpacity: mode === "solid" ? 1 : 0,
    stroke: color,
    strokeOpacity: mode === "outline" ? 1 : 0,
    strokeWidth: mode === "outline" ? 0.2 : 0,
  };

  return <circle cx={center.x} cy={-center.y} r={radius} style={style} />;
}

interface LimaconProps {
  limacon: IPetal;
}

function Limacon(props: LimaconProps) {
  const { center, angle, size, color, mode } = props.limacon;

  const getInterpolatedPoints = (steps: number) => {
    const points: Point[] = [];

    for (let theta = 0; theta <= 2 * Math.PI; theta += (2 * Math.PI) / steps) {
      const a = size / 3;
      const b = size / 1.5;
      const r = a + b * Math.cos(theta - angle);

      let { x, y } = polarToCartesian(r, theta);
      x += center.x;
      y += center.y;

      points.push({ x, y: -y });
    }

    return points.map(({ x, y }) => `${x} ${y}`).join(",");
  };

  const style: React.CSSProperties = {
    fill: color,
    fillOpacity: mode === "solid" ? 1 : 0,
    stroke: color,
    strokeOpacity: mode === "outline" ? 1 : 0,
    strokeWidth: mode === "outline" ? 0.2 : 0,
  };

  return <polyline points={getInterpolatedPoints(360)} style={style} />;
}

interface LineProps {
  line: ILine;
}

function Line(props: LineProps) {
  const { endpoints, color } = props.line;

  const style: React.CSSProperties = {
    stroke: color,
    strokeWidth: 0.2,
  };

  return (
    <line
      x1={endpoints[0].x}
      y1={-endpoints[0].y}
      x2={endpoints[1].x}
      y2={-endpoints[1].y}
      style={style}
    />
  );
}

interface PetalProps {
  petal: IPetal;
}

function Petal(props: PetalProps) {
  const { center, angle, size, color, mode } = props.petal;

  const getInterpolatedPoints = (steps: number) => {
    const points: Point[] = [];

    for (
      let theta = -Math.PI / 4 + angle;
      theta <= Math.PI / 4 + angle;
      theta += Math.PI / 2 / steps
    ) {
      const r = size * Math.cos(2 * (theta - angle));

      let { x, y } = polarToCartesian(r, theta);
      x += center.x;
      y += center.y;

      points.push({ x, y: -y });
    }

    return points.map(({ x, y }) => `${x} ${y}`).join(",");
  };

  const style: React.CSSProperties = {
    fill: color,
    fillOpacity: mode === "solid" ? 1 : 0,
    stroke: color,
    strokeOpacity: mode === "outline" ? 1 : 0,
    strokeWidth: mode === "outline" ? 0.2 : 0,
  };

  return <polyline points={getInterpolatedPoints(360)} style={style} />;
}

interface RendererProps {
  shapes: TShape[];
  error: Error | null;
}

function Renderer(props: RendererProps): JSX.Element {
  const { shapes, error } = props;
  const [showGrid, setShowGrid] = useState(true);
  const axisLength = 20;

  const styles: { [name: string]: React.CSSProperties } = {
    root: {
      display: "flex",
      flexDirection: "column",
    },
    gridToggle: {
      marginLeft: "auto",
      fontSize: "12pt",
    },
    canvas: {
      width: "min(40vw, 80vh)",
      height: "min(40vw, 80vh)",
      borderWidth: 1,
      borderStyle: "solid",
      borderColor: "black",
      backgroundColor: "white",
    },
  };

  return (
    <div className="Renderer" style={styles.root}>
      <label style={styles.gridToggle}>
        <input
          type="checkbox"
          checked={showGrid}
          onChange={() => setShowGrid(!showGrid)}
        />
        Show grid
      </label>
      <svg
        style={styles.canvas}
        viewBox={`-${axisLength} -${axisLength} ${axisLength * 2} ${
          axisLength * 2
        }`}
      >
        {showGrid && !error ? <GridLines axisLength={axisLength} /> : null}
        <ErrorMessage error={error} />
        {[...shapes].reverse().map((shape) => {
          switch (shape.type) {
            case "cardioid":
              return <Cardioid cardioid={shape} />;
            case "circle":
              return <Circle circle={shape} />;
            case "limacon":
              return <Limacon limacon={shape} />;
            case "line":
              return <Line line={shape} />;
            case "petal":
              return <Petal petal={shape} />;
            default:
              return null;
          }
        })}
      </svg>
    </div>
  );
}

export default Renderer;
