/* eslint-disable @typescript-eslint/no-non-null-assertion */
import {
  AopContext,
  ArithexpContext,
  AssignmentContext,
  CardioidContext,
  CircleContext,
  ColorContext,
  CompexpContext,
  CopContext,
  DrawContext,
  ForloopContext,
  IfblockContext,
  LimaconContext,
  LineContext,
  ObjectContext,
  OverlayContext,
  PetalContext,
  ProgramContext,
  RepeatContext,
  RotateContext,
  ShapeConstructorContext,
  ShapeContext,
  ShapeModeContext,
  StatementContext,
  StatementsContext,
  TransformationContext,
  VariableContext,
} from "./antlr/MandalaParser";
import { MandalaParserVisitor } from "./antlr/MandalaParserVisitor";
import { SyntaxChecker } from "./SyntaxChecker";
import { Element } from "../ast/Element";
import { TerminalNode } from "antlr4ts/tree/TerminalNode";
import { ErrorNode, ParseTree, RuleNode } from "antlr4ts/tree";
import {
  Article,
  ArticleAssignment,
  Assignment,
  Draw,
  NumAssignment,
  Program,
  Statement,
} from "../ast/Program";
import { ForLoop, IfBlock } from "../ast/ControlBlocks";
import { ArithExp, ArithOp, CompExp, CompOp } from "../ast/Expressions";
import { EvaluatesToNumber } from "../ast/EvaluatesToNumber";
import { ArticleVariable, NumberVariable } from "../ast/Variables";
import { Num } from "../ast/Num";
import {
  Cardioid,
  Circle,
  Limacon,
  Line,
  Mode,
  Petal,
  Shape,
} from "../ast/Shapes";
import { Overlay, Repetition, Rotation } from "../ast/Transformations";

export class ParseTreeToAST implements MandalaParserVisitor<Element> {
  // First checks the given program's syntax, throws an error
  // if the checker does not return an empty string
  // Get the parsed tree and traverse it starting from the program
  // returns the program object with statements and a draw instruction
  visitProgram(ctx: ProgramContext): Program {
    const errorString = SyntaxChecker.visitProgram(ctx);
    if (errorString !== "") {
      throw new Error(errorString);
    }
    const statements: Statement[] = this.getStatements(ctx.statements());
    const draw: Draw = this.visitDraw(ctx.draw());
    return new Program(statements, draw);
  }

  // Visits list of statements and returns list of
  // statement objects
  getStatements(ctx: StatementsContext): Statement[] {
    const statements: Statement[] = [];
    ctx.statement().forEach((s: StatementContext) => {
      statements.push(this.visitStatement(s));
    });
    return statements;
  }

  // Creates the draw object
  visitDraw(ctx: DrawContext): Draw {
    return new Draw(this.visitObject(ctx.object()));
  }

  // Returns a statement
  // Each statement is one of: an assignment,
  // a forloop, or an ifblock throws an error
  // if it's non of them
  visitStatement(ctx: StatementContext): Statement {
    if (ctx.assignment() != undefined) {
      return this.visitAssignment(ctx.assignment()!);
    } else if (ctx.forloop() != undefined) {
      return this.visitForloop(ctx.forloop()!);
    } else if (ctx.ifblock() != undefined) {
      return this.visitIfblock(ctx.ifblock()!);
    } else {
      throw new Error("Incorrect statement " + ctx.text);
    }
  }

  // --------------------Statements--------------------

  // Returns an assignment
  // Each assignment is either an article or an arithmetic
  // expression with a name, throws an error if it's neither
  // Article is one of: shape, transformation, shape variable
  visitAssignment(ctx: AssignmentContext): Assignment {
    const varName: string = ctx.TEXT().toString();
    if (ctx.shape() != undefined) {
      return new ArticleAssignment(varName, this.visitShape(ctx.shape()!));
    } else if (ctx.arithexp() != undefined) {
      return new NumAssignment(varName, this.visitArithexp(ctx.arithexp()!));
    } else {
      throw new Error("Incorrect assignment " + ctx.text);
    }
  }

  // Returns an object
  // An object is either an article (ie non arithmetic)
  // variable or a shape throws an error if it's neither
  visitObject(ctx: ObjectContext): Article {
    if (ctx.variable() != undefined) {
      return new ArticleVariable(this.getVariable(ctx.variable()!));
    } else if (ctx.shape() != undefined) {
      return this.visitShape(ctx.shape()!);
    } else {
      throw new Error("Incorrect object: " + ctx.text);
    }
  }

  // Returns a shape
  // A shape is either a shape constructor or a transformation
  // throws an error if it's neither
  visitShape(ctx: ShapeContext): Article {
    if (ctx.shapeConstructor() != undefined) {
      return this.visitShapeConstructor(ctx.shapeConstructor()!);
    } else if (ctx.transformation() != undefined) {
      return this.visitTransformation(ctx.transformation()!);
    } else {
      throw new Error("Incorrect object: " + ctx.text);
    }
  }

  // --------------------Transformations--------------------

  // Transformations

  // Returns a transformation
  // A tranformation is one of: rotation, repetition,
  // or overlay throws an error if it's non of them
  visitTransformation(ctx: TransformationContext): Article {
    if (ctx.rotate() != undefined) {
      return this.visitRotate(ctx.rotate()!);
    } else if (ctx.repeat() != undefined) {
      return this.visitRepeat(ctx.repeat()!);
    } else if (ctx.overlay() != undefined) {
      return this.visitOverlay(ctx.overlay()!);
    } else {
      throw new Error("Incorrect transformation: " + ctx.text);
    }
  }

  // Returns a rotation object with an article and an
  // arithmetic expression (angle)
  visitRotate(ctx: RotateContext): Article {
    const shape: Article = this.visitObject(ctx.object());
    const angle: ArithExp = this.visitArithexp(ctx.arithexp());
    return new Rotation(shape, angle);
  }

  // Returns a repetition object with an article, and two
  // arithmetic expressions (repetition number, and offset)
  visitRepeat(ctx: RepeatContext): Article {
    const shape: Article = this.visitObject(ctx.object());
    const repNum: ArithExp = this.visitArithexp(ctx.arithexp(0));
    const offset: ArithExp = this.visitArithexp(ctx.arithexp(1));
    return new Repetition(shape, repNum, offset);
  }

  // Returns an overlay object with list of articles
  visitOverlay(ctx: OverlayContext): Article {
    const shapes: Article[] = [];
    ctx.object().forEach((o: ObjectContext) => {
      shapes.push(this.visitObject(o));
    });
    return new Overlay(shapes);
  }

  // --------------------Shape constructors--------------------

  // Returns a shape constructor object
  // It is one of: circle, line, petal, cardioid, or limacon
  // throws an error if it's non of them
  visitShapeConstructor(ctx: ShapeConstructorContext): Article {
    if (ctx.circle() != undefined) {
      return this.visitCircle(ctx.circle()!);
    } else if (ctx.line() != undefined) {
      return this.visitLine(ctx.line()!);
    } else if (ctx.petal() != undefined) {
      return this.visitPetal(ctx.petal()!);
    } else if (ctx.cardioid() != undefined) {
      return this.visitCardioid(ctx.cardioid()!);
    } else if (ctx.limacon() != undefined) {
      return this.visitLimacon(ctx.limacon()!);
    } else {
      throw new Error("Incorrect shape: " + ctx.text);
    }
  }

  // Returns a circle with radius, color, and mode (outline or solid)
  visitCircle(ctx: CircleContext): Shape {
    const r: EvaluatesToNumber = this.visitArithexp(ctx.arithexp());
    const color: string = this.getColor(ctx.color());
    const mode: Mode = this.getShapeMode(ctx.shapeMode());
    return new Circle(r, color, mode);
  }

  // Returns a line with length, and color
  visitLine(ctx: LineContext): Shape {
    const size: EvaluatesToNumber = this.visitArithexp(ctx.arithexp());
    const color: string = this.getColor(ctx.color());
    return new Line(size, color);
  }

  // Returns a petal with size, color, and mode (outline or solid)
  visitPetal(ctx: PetalContext): Shape {
    const size: EvaluatesToNumber = this.visitArithexp(ctx.arithexp());
    const color: string = this.getColor(ctx.color());
    const mode: Mode = this.getShapeMode(ctx.shapeMode());
    return new Petal(size, color, mode);
  }

  // Returns a cardioid with size, color, and mode (outline or solid)
  visitCardioid(ctx: CardioidContext): Shape {
    const size: EvaluatesToNumber = this.visitArithexp(ctx.arithexp());
    const color: string = this.getColor(ctx.color());
    const mode: Mode = this.getShapeMode(ctx.shapeMode());
    return new Cardioid(size, color, mode);
  }

  // Returns a limacon with size, color, and mode (outline or solid)
  visitLimacon(ctx: LimaconContext): Shape {
    const size: EvaluatesToNumber = this.visitArithexp(ctx.arithexp());
    const color: string = this.getColor(ctx.color());
    const mode: Mode = this.getShapeMode(ctx.shapeMode());
    return new Limacon(size, color, mode);
  }

  // --------------------Control Flows--------------------

  // Returns a forloop object with counter variable
  // range numbers start and end and list of statements
  // in its body
  visitForloop(ctx: ForloopContext): ForLoop {
    const counter: string = this.getVariable(ctx.variable());
    const rangeStart: EvaluatesToNumber = this.visitArithexp(ctx.arithexp(0));
    const rangeEnd: EvaluatesToNumber = this.visitArithexp(ctx.arithexp(1));
    const statements: Statement[] = this.getStatements(ctx.statements());
    return new ForLoop(counter, rangeStart, rangeEnd, statements);
  }

  // Returns an ifblock object with comparison expression
  // list of statements in the then block and if it has an
  // else block, list of statements in the else block
  visitIfblock(ctx: IfblockContext): IfBlock {
    const comp: CompExp = this.visitCompexp(ctx.compexp());
    const thenStatements: Statement[] = [];
    ctx._thenblock.statement().forEach((s: StatementContext) => {
      thenStatements.push(this.visitStatement(s));
    });
    if (ctx.ELSE() != undefined) {
      const elseStatements: Statement[] = [];
      ctx._elseblock.statement().forEach((s: StatementContext) => {
        elseStatements.push(this.visitStatement(s));
      });
      return new IfBlock(comp, thenStatements, elseStatements);
    }
    return new IfBlock(comp, thenStatements);
  }

  // --------------------Expressions--------------------

  // Returns an arithmetic expression is one of
  // number variable, or a number
  // if it has more arithmetic expressions it also has
  // an arithmetic operation with more expressions
  // throws an error if it's non of them
  visitArithexp(ctx: ArithexpContext): ArithExp {
    let value: EvaluatesToNumber;
    if (ctx.variable() != undefined) {
      value = new NumberVariable(this.getVariable(ctx.variable()!));
    } else if (ctx.NUM() != undefined) {
      value = new Num(parseInt(ctx.NUM()!.toString(), 10));
    } else {
      throw new Error(
        "Incorrect value for the arithmetic expression: " + ctx.text,
      );
    }
    if (ctx.aop() != undefined && ctx.arithexp() != undefined) {
      const aop: ArithOp = this.getAop(ctx.aop()!);
      const arithexp: ArithExp = this.visitArithexp(ctx.arithexp()!);
      return new ArithExp(value, aop, arithexp);
    }
    return new ArithExp(value);
  }

  // Returns a comparison object with two arithmetic
  // expressions and a comparison operation
  visitCompexp(ctx: CompexpContext): CompExp {
    const arith1: ArithExp = this.visitArithexp(ctx.arithexp(0));
    const cop: CompOp = this.getCop(ctx.cop());
    const arith2: ArithExp = this.visitArithexp(ctx.arithexp(1));
    return new CompExp(arith1, arith2, cop);
  }

  // --------------------Expressions--------------------

  // Gets the color string
  // has to be one of: red orange yellow green
  // blue black white or a hex representation of
  // a color throws an error if not
  getColor(ctx: ColorContext): string {
    if (ctx.RED() != undefined) {
      return "red";
    } else if (ctx.ORANGE() != undefined) {
      return "orange";
    } else if (ctx.YELLOW() != undefined) {
      return "yellow";
    } else if (ctx.GREEN() != undefined) {
      return "green";
    } else if (ctx.BLUE() != undefined) {
      return "blue";
    } else if (ctx.BLACK() != undefined) {
      return "black";
    } else if (ctx.WHITE() != undefined) {
      return "white";
    } else if (ctx.HEXCOLOR() != undefined) {
      return ctx.HEXCOLOR()!.toString();
    } else {
      throw new Error("Incorrect color: " + ctx.text);
    }
  }

  // Gets the shape mode string
  // has to be one of: outline, solid
  // throws an error if not
  getShapeMode(ctx: ShapeModeContext): Mode {
    if (ctx.OUTLINE() != undefined) {
      return "outline";
    } else if (ctx.SOLID() != undefined) {
      return "solid";
    } else {
      throw new Error("Incorrect shape mode: " + ctx.text);
    }
  }

  // Gets the variable string
  getVariable(ctx: VariableContext): string {
    return ctx.TEXT().toString();
  }

  // Gets the arithmetic operation string
  // it has to be one of: + - * / % throws an
  // error if not
  getAop(ctx: AopContext): ArithOp {
    if (ctx.ADD() != undefined) {
      return "+";
    } else if (ctx.SUB() != undefined) {
      return "-";
    } else if (ctx.MULT() != undefined) {
      return "*";
    } else if (ctx.DIV() != undefined) {
      return "/";
    } else if (ctx.MOD() != undefined) {
      return "%";
    } else {
      throw new Error("Incorrect arithmetic operation: " + ctx.text);
    }
  }

  // Gets the comparison operation string
  // it has to be one of: < <= > >= == !=
  // throws an error if not
  getCop(ctx: CopContext): CompOp {
    if (ctx.LT() != undefined) {
      return "<";
    } else if (ctx.LTE() != undefined) {
      return "<=";
    } else if (ctx.GT() != undefined) {
      return ">";
    } else if (ctx.GTE() != undefined) {
      return ">=";
    } else if (ctx.EQ() != undefined) {
      return "==";
    } else if (ctx.NEQ() != undefined) {
      return "!=";
    } else {
      throw new Error("Incorrect conditional: " + ctx.text);
    }
  }

  // Extra methods from MandalaParser.ts
  // Unused methods that are in the MandalaParser interface generated
  // by antlr
  visit(tree: ParseTree): Element {
    throw new Error("Visit Tree is not implemented " + tree.text);
  }

  visitChildren(node: RuleNode): Element {
    throw new Error("Visit Children is not implemented " + node.text);
  }

  visitErrorNode(node: ErrorNode): Element {
    throw new Error("Visit Error Node is not implemented " + node.text);
  }

  visitTerminal(node: TerminalNode): Element {
    throw new Error("Visit Terminal is not implemented " + node.text);
  }
}
