import { CompExp } from "./Expressions";
import { Statement } from "./Program";
import { EvaluatesToNumber } from "./EvaluatesToNumber";
import { Visitor } from "./Visitor";
import { Element } from "./Element";

// Control Blocks: For loop, if statements

export class ForLoop implements Element {
  counter: string;
  rangeStart: EvaluatesToNumber;
  rangeEnd: EvaluatesToNumber;
  statements: Statement[];

  constructor(
    counter: string,
    rangeStart: EvaluatesToNumber,
    rangeEnd: EvaluatesToNumber,
    statements: Statement[],
  ) {
    this.counter = counter;
    this.rangeStart = rangeStart;
    this.rangeEnd = rangeEnd;
    this.statements = statements;
  }

  accept<JType, NumType, CType>(visitor: Visitor<JType, NumType, CType>): void {
    visitor.visitForLoop(this);
  }
}

export class IfBlock implements Element {
  condition: CompExp;
  ifStatements: Statement[];
  elseStatements: Statement[] | undefined;

  constructor(
    condition: CompExp,
    ifStatements: Statement[],
    elseStatements?: Statement[],
  ) {
    this.condition = condition;
    this.ifStatements = ifStatements;
    this.elseStatements = elseStatements;
  }

  accept<JType, NumType, CType>(visitor: Visitor<JType, NumType, CType>): void {
    visitor.visitIfBlock(this);
  }
}
