import React, { useState } from "react";

interface EditorProps {
  onCodeChange: (code: string) => void;
  onClickRun: (code: string) => void;
}

function Editor(props: EditorProps): JSX.Element {
  const { onCodeChange, onClickRun } = props;
  const [code, setCode] = useState<string>("");

  const handleChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
    const code = event.target.value;
    setCode(code);
    onCodeChange(code);
  };

  const handleSubmit = (event: React.MouseEvent<HTMLButtonElement>) => {
    event.preventDefault();
    onClickRun(code);
  };

  const styles: { [name: string]: React.CSSProperties } = {
    root: {
      display: "flex",
      flexDirection: "column",
    },
    docs: {
      color: "#3178c6",
      textDecoration: "none",
    },
    button: {
      float: "right",
      height: 20,
      fontSize: "12pt",
      lineHeight: 0,
      padding: "3px 10px",
      border: "none",
      borderRadius: "5px 5px 0 0",
      backgroundColor: "#76b947",
      color: "white",
    },
    textArea: {
      margin: 0,
      width: "calc(min(40vw, 80vh) - 6px)",
      height: "calc(min(40vw, 80vh) - 6px)",
      resize: "none",
      fontSize: "12pt",
      border: "1px solid black",
      outline: "none",
    },
  };

  return (
    <form className="Editor" style={styles.root}>
      <div>
        <a
          href="https://github.com/JohannCooper/johanncooper.github.io/blob/main/DOCS.md"
          style={styles.docs}
        >
          Docs
        </a>
        <button style={styles.button} onClick={handleSubmit}>
          Run
        </button>
      </div>
      <textarea
        style={styles.textArea}
        placeholder="Insert code here"
        value={code}
        onChange={handleChange}
        spellCheck={false}
      />
    </form>
  );
}

export default Editor;
