import { Element } from "./Element";
import { EvaluatesToNumber } from "./EvaluatesToNumber";
import { Visitor } from "./Visitor";

// Expressions: Arithmetic Expressions, Comparison Expressions

export type ArithOp = "+" | "-" | "*" | "/" | "%";
export type CompOp = "<" | ">" | "<=" | ">=" | "==" | "!=";

export class ArithExp implements EvaluatesToNumber {
  value: EvaluatesToNumber;
  arithOp: ArithOp | undefined;
  otherExp: ArithExp | undefined;

  constructor(value: EvaluatesToNumber);
  constructor(value: EvaluatesToNumber, arithOp: ArithOp, otherExp: ArithExp);
  constructor(
    value: EvaluatesToNumber,
    arithOp?: ArithOp,
    otherExp?: ArithExp,
  ) {
    this.value = value;
    this.arithOp = arithOp;
    this.otherExp = otherExp;
  }

  accept<JType, NumType, CType>(
    visitor: Visitor<JType, NumType, CType>,
  ): NumType {
    return visitor.visitArithExp(this);
  }
}

export class CompExp implements Element {
  leftArithExp: ArithExp;
  rightArithExp: ArithExp;
  compOp: CompOp;

  constructor(leftAE: ArithExp, rightAE: ArithExp, compOp: CompOp) {
    this.leftArithExp = leftAE;
    this.rightArithExp = rightAE;
    this.compOp = compOp;
  }

  accept<JType, NumType, CType>(
    visitor: Visitor<JType, NumType, CType>,
  ): CType {
    return visitor.visitCompExp(this);
  }
}
