import React from "react";
import ReactDOM from "react-dom";
import { Interpreter } from "./interpreter/interpreter";
import App from "./ui/App";
import { Shape } from "./ui/types";
import { Main } from "./parser/Main";
import { JShape } from "./ui/JShapes";
import { Validator } from "./validator";
import "./styles.css";

function onCodeChange(): void {
  return;
}

// When run is clicked takes the input code
// creates ast, checks errors and then
// interprets the program. Returns the created
// shapes if no error is thrown
function onClickRun(code: string): Shape[] {
  const parsedProgram = Main.parseCode(code);

  const validator: Validator = new Validator();
  validator.visitProgram(parsedProgram);

  const interpreter: Interpreter = new Interpreter();
  const shapes: JShape[] = interpreter.visitProgram(parsedProgram);

  return shapes;
}

ReactDOM.render(
  <React.StrictMode>
    <App onCodeChange={onCodeChange} onClickRun={onClickRun} />
  </React.StrictMode>,
  document.getElementById("root"),
);
