import React, { useState } from "react";
import Header from "./components/Header";
import Editor from "./components/Editor";
import Renderer from "./components/Renderer";
import { Shape } from "./types";

interface AppProps {
  onCodeChange: (code: string) => void;
  onClickRun: (code: string) => Shape[];
}

function App(props: AppProps): JSX.Element {
  const { onCodeChange, onClickRun } = props;
  const [shapes, setShapes] = useState<Shape[]>([]);
  const [error, setError] = useState<Error | null>(null);

  const handleClickRun = (code: string) => {
    try {
      const shapes = onClickRun(code);
      setShapes(shapes);
      setError(null);
    } catch (error) {
      setShapes([]);
      setError(error as Error);
    }
  };

  const styles: { [name: string]: React.CSSProperties } = {
    app: {
      fontFamily: "Roboto, sans-serif",
      backgroundColor: "#fafafa",
    },
    body: {
      marginTop: "2vh",
      display: "flex",
      justifyContent: "space-evenly",
    },
  };

  return (
    <div className="App" style={styles.app}>
      <Header />
      <div style={styles.body}>
        <Editor onCodeChange={onCodeChange} onClickRun={handleClickRun} />
        <Renderer shapes={shapes} error={error} />
      </div>
    </div>
  );
}

export default App;
