import { ArithOp, CompOp } from "../ast/Expressions";
import { Point } from "../ui/types";

/**
 * Converts a polar coordinate to a cartesian coordinate
 * @param r the distance from the origin
 * @param theta the angle about the origin in radians
 * @returns the equivalent cartesian coordinate
 */
export function polarToCartesian(r: number, theta: number): Point {
  return {
    x: r * Math.cos(theta),
    y: r * Math.sin(theta),
  };
}

// Rotates a polar coordinate about the origin
// by angle theta (in radians)
export function rotateAboutOrigin(point: Point, theta: number): Point {
  return {
    x: point.x * Math.cos(theta) - point.y * Math.sin(theta),
    y: point.x * Math.sin(theta) + point.y * Math.cos(theta),
  };
}

// Converts an angle in degrees to radians
export function degToRad(degrees: number): number {
  return (degrees * Math.PI) / 180;
}

// Evaluates an arithmetic expression
export function evaluateAE(
  leftVal: number,
  rightVal: number,
  op: ArithOp,
): number {
  switch (op) {
    case "+": {
      return leftVal + rightVal;
    }
    case "-": {
      return leftVal - rightVal;
    }
    case "*": {
      return leftVal * rightVal;
    }
    case "/": {
      if (rightVal == 0) {
        throw new Error(
          "Cannot divide by 0 (attempted to divide " + leftVal + " by 0).",
        );
      }
      return leftVal / rightVal;
    }
    case "%": {
      return leftVal % rightVal;
    }
  }
}

// Evaluates an arithmetic expression
export function evaluateComparison(
  leftVal: number,
  rightVal: number,
  op: CompOp,
): boolean {
  switch (op) {
    case "<": {
      return leftVal < rightVal;
    }
    case ">": {
      return leftVal > rightVal;
    }
    case "<=": {
      return leftVal <= rightVal;
    }
    case ">=": {
      return leftVal >= rightVal;
    }
    case "==": {
      return leftVal === rightVal;
    }
    case "!=": {
      return leftVal !== rightVal;
    }
  }
}

// Checks that the number is positive
export function greaterThanZero(value: number, msg: string): void {
  if (!(value > 0)) {
    throw new Error(
      "Number (" +
        msg +
        ") must be greater than 0. Instead, evaluated to " +
        value +
        ".",
    );
  }
}
