class CustomError extends Error {
  constructor(name: string, message: string) {
    const _message = `${name}: "${message}"`;
    super(_message);
    this.name = name;
  }
}

// Custom errors

// Custom error for duplicate variables
export class DuplicateVariableError extends CustomError {
  constructor(variableName: string) {
    const name = "UndefinedVariableError";
    const message = `'${variableName}' is already defined`;
    super(name, message);
  }
}

// Custom error for incorrect types
export class TypeError extends CustomError {
  constructor(variableName: string, expectedType: string) {
    const name = "TypeError";
    const message = `'${variableName}' is not of type '${expectedType}'`;
    super(name, message);
  }
}

// Custom error for undefined variables
export class UndefinedVariableError extends CustomError {
  constructor(variableName: string) {
    const name = "UndefinedVariableError";
    const message = `'${variableName}' is not defined`;
    super(name, message);
  }
}
