import { Circle, Line, Petal, PetalType, Point } from "./types";
import { rotateAboutOrigin } from "../utils/math_utils";
import { Mode } from "../ast/Shapes";

export type JShape = JCircle | JLine | JPetal;

// JShapes and their methods

export class JCircle implements Circle {
  type: "circle" = "circle";
  center: Point;
  radius: number;
  color: string;
  mode: Mode;

  constructor(center: Point, radius: number, color: string, mode: Mode) {
    this.center = center;
    this.radius = radius;
    this.color = color;
    this.mode = mode;
  }

  // Returns the replicated circle
  copy(): JCircle {
    const centerCopy: Point = { x: this.center.x, y: this.center.y };
    return new JCircle(centerCopy, this.radius, this.color, this.mode);
  }

  // Rotates the circle around the origin given an angle
  // (Only has an effect when the circle is not centered on the origin)
  rotate(angle: number): void {
    this.center = rotateAboutOrigin(this.center, angle);
  }

  // Repeats the circle reps times around the center
  // with the offset from the center while also
  // rotating it around the center
  repeat(reps: number, offset: number): JCircle[] {
    this.center.x += offset;
    const repeatedShapes: JCircle[] = [this];

    const angle: number = (2 * Math.PI) / reps;
    let rotatingPoint: Point = this.center;

    for (let i = 1; i < reps; i++) {
      rotatingPoint = rotateAboutOrigin(rotatingPoint, angle);
      repeatedShapes.push(
        new JCircle(rotatingPoint, this.radius, this.color, this.mode),
      );
    }

    return repeatedShapes;
  }
}

export class JLine implements Line {
  type: "line" = "line";
  endpoints: [Point, Point];
  color: string;

  constructor(endpoints: [Point, Point], color: string) {
    this.endpoints = endpoints;
    this.color = color;
  }

  // Returns the replicated line
  copy(): JLine {
    const [start, end] = this.endpoints;
    const endpointsCopy: [Point, Point] = [
      { x: start.x, y: start.y },
      { x: end.x, y: end.y },
    ];
    return new JLine(endpointsCopy, this.color);
  }

  // Rotates the line given an angle
  rotate(angle: number): void {
    const [start, end]: [Point, Point] = this.endpoints;
    const newStart: Point = rotateAboutOrigin(start, angle);
    const newEnd: Point = rotateAboutOrigin(end, angle);
    this.endpoints = [newStart, newEnd];
  }

  // Repeats the line reps times around the center
  // with the offset from the center while also
  // rotating it around the center
  repeat(reps: number, offset: number): JLine[] {
    this.endpoints[0].x += offset;
    this.endpoints[1].x += offset;

    const repeatedShapes: JLine[] = [this];

    const angle: number = (2 * Math.PI) / reps;
    let [rotatedStart, rotatedEnd]: [Point, Point] = this.endpoints;

    for (let i = 1; i < reps; i++) {
      rotatedStart = rotateAboutOrigin(rotatedStart, angle);
      rotatedEnd = rotateAboutOrigin(rotatedEnd, angle);

      repeatedShapes.push(new JLine([rotatedStart, rotatedEnd], this.color));
    }

    return repeatedShapes;
  }
}

export class JPetal implements Petal {
  type: PetalType;
  center: Point;
  angle: number; // in radians
  size: number;
  color: string;
  mode: Mode;

  constructor(
    type: PetalType,
    center: Point,
    angle: number,
    size: number,
    color: string,
    mode: Mode,
  ) {
    this.type = type;
    this.center = center;
    this.angle = angle;
    this.size = size;
    this.color = color;
    this.mode = mode;
  }

  // Returns the replicated petal
  copy(): JPetal {
    const centerCopy: Point = { x: this.center.x, y: this.center.y };
    return new JPetal(
      this.type,
      centerCopy,
      this.angle,
      this.size,
      this.color,
      this.mode,
    );
  }

  // Rotates the petal given an angle
  rotate(angle: number): void {
    this.center = rotateAboutOrigin(this.center, angle);
    this.angle = this.angle + angle;
  }

  // Repeats the petal reps times around the center
  // with the offset from the center while also
  // rotating it around the center
  repeat(reps: number, offset: number): JPetal[] {
    this.center.x += offset;
    const repeatedShapes: JPetal[] = [this];

    const angle: number = (2 * Math.PI) / reps;
    let rotatingPoint: Point = this.center;

    for (let i = 1; i < reps; i++) {
      rotatingPoint = rotateAboutOrigin(rotatingPoint, angle);
      repeatedShapes.push(
        new JPetal(
          this.type,
          rotatingPoint,
          this.angle + angle * i,
          this.size,
          this.color,
          this.mode,
        ),
      );
    }

    return repeatedShapes;
  }
}
