import { Element } from "./Element";
import { EvaluatesToNumber } from "./EvaluatesToNumber";
import { Visitor } from "./Visitor";

// Circle:    radius, color, mode
// Line:      length, color
// Petal:     size, color, mode
// Cardioid:  size, color, mode
// Limacon:   size, color, mode

export type Shape = Circle | Line | Petal | Cardioid | Limacon;

export type Mode = "solid" | "outline";

export class Circle implements Element {
  radius: EvaluatesToNumber;
  color: string;
  mode: Mode;

  constructor(radius: EvaluatesToNumber, color: string, mode: Mode) {
    this.radius = radius;
    this.color = color;
    this.mode = mode;
  }

  accept<JType, NumType, CType>(
    visitor: Visitor<JType, NumType, CType>,
  ): JType {
    return visitor.visitCircle(this);
  }
}

