/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable @typescript-eslint/no-non-null-assertion */
import {
  AopContext,
  ArithexpContext,
  AssignmentContext,
  CardioidContext,
  CircleContext,
  ColorContext,
  CompexpContext,
  CopContext,
  DrawContext,
  ForloopContext,
  IfblockContext,
  LimaconContext,
  LineContext,
  ObjectContext,
  OverlayContext,
  PetalContext,
  ProgramContext,
  RepeatContext,
  RotateContext,
  ShapeConstructorContext,
  ShapeContext,
  ShapeModeContext,
  StatementContext,
  StatementsContext,
  TransformationContext,
  VariableContext,
} from "./antlr/MandalaParser";

// Checks the parsed tree to see if it's missing any punctuations
// builds up an error string as it traverses the tree

export class SyntaxChecker {
  // Visits program's nodes (statements and draw) and
  // appends to the string
  public static visitProgram(ctx: ProgramContext): string {
    let str = "";
    str = str.concat(this.getStatements(ctx.statements()));
    str = str.concat(this.visitDraw(ctx.draw()));
    return str;
  }

  static getStatements(ctx: StatementsContext): string {
    let str = "";
    ctx.statement().forEach((s: StatementContext) => {
      str = str.concat(this.visitStatement(s));
    });
    return str;
  }

  // Visits statement's nodes (assingment, forloop, and ifblock),
  // and appends to the string
  static visitStatement(ctx: StatementContext): string {
    let str = "";
    if (ctx.assignment() != undefined) {
      str = str.concat(this.visitAssignment(ctx.assignment()!));
    } else if (ctx.forloop() != undefined) {
      str = str.concat(this.visitForloop(ctx.forloop()!));
    } else if (ctx.ifblock() != undefined) {
      str = str.concat(this.visitIfblock(ctx.ifblock()!));
    }
    return str;
  }

  // Visits assignment's nodes (shape, and arithexp),
  // appends to the string, checks if it's missing
  // ; or =
  static visitAssignment(ctx: AssignmentContext): string {
    let str = "";
    if (ctx.shape() != undefined) {
      str = str.concat(this.visitShape(ctx.shape()!));
    } else if (ctx.arithexp() != undefined) {
      str = str.concat(this.visitArithexp(ctx.arithexp()!));
    }
    if (ctx.SEMICOLON().toString() !== ";") {
      str = str.concat("Assignment " + ctx.text + " is missing ;\n");
    }
    if (ctx.SET().toString() !== "=") {
      str = str.concat("Assignment " + ctx.text + " is missing =\n");
    }
    return str;
  }

  // Visits draws's node (object),
  // appends to the string, checks if it's missing
  // ;
  static visitDraw(ctx: DrawContext): string {
    let str = "";
    str = str.concat(this.visitObject(ctx.object()));
    if (ctx.SEMICOLON().toString() !== ";") {
      str = str.concat("Assignment " + ctx.text + " is missing ;\n");
    }
    return str;
  }

  // Visits objects's nodes (variable, and shape),
  // and appends to the string
  static visitObject(ctx: ObjectContext): string {
    let str = "";
    if (ctx.variable() != undefined) {
      str = str.concat(this.getVariable(ctx.variable()!));
    } else if (ctx.shape() != undefined) {
      str = str.concat(this.visitShape(ctx.shape()!));
    }
    return str;
  }

  // Visits shapes's nodes (shape constructor, and
  // transformation), and appends to the string
  static visitShape(ctx: ShapeContext): string {
    let str = "";
    if (ctx.shapeConstructor() != undefined) {
      str = str.concat(this.visitShapeConstructor(ctx.shapeConstructor()!));
    } else if (ctx.transformation() != undefined) {
      str = str.concat(this.visitTransformation(ctx.transformation()!));
    }
    return str;
  }

  // Transformations
  // Visits transformation's nodes (rotate, repeat, and overlay),
  // and appends to the string
  static visitTransformation(ctx: TransformationContext): string {
    let str = "";
    if (ctx.rotate() != undefined) {
      str = str.concat(this.visitRotate(ctx.rotate()!));
    } else if (ctx.repeat() != undefined) {
      str = str.concat(this.visitRepeat(ctx.repeat()!));
    } else if (ctx.overlay() != undefined) {
      str = str.concat(this.visitOverlay(ctx.overlay()!));
    }
    return str;
  }

  // Visits rotate's nodes (object, and arithexp),
  // appends to the string. Checks if it's missing
  // , or )
  static visitRotate(ctx: RotateContext): string {
    let str = "";
    str = str.concat(this.visitObject(ctx.object()));
    str = str.concat(this.visitArithexp(ctx.arithexp()));
    if (ctx.COMMA().toString() !== ",") {
      str = str.concat("Rotation " + ctx.text + " is missing ,\n");
    }
    if (ctx.END_FUNCTION().toString() !== ")") {
      str = str.concat("Rotation " + ctx.text + " is missing )\n");
    }
    return str;
  }

  // Visits repeat's nodes (object, and arithexps),
  // appends to the string. Checks if it's missing
  // , or )
  static visitRepeat(ctx: RepeatContext): string {
    let str = "";
    str = str.concat(this.visitObject(ctx.object()));
    str = str.concat(this.visitArithexp(ctx.arithexp(0)));
    str = str.concat(this.visitArithexp(ctx.arithexp(1)));
    if (ctx.COMMA(0).toString() !== "," || ctx.COMMA(1).toString() !== ",") {
      str = str.concat("Repetition " + ctx.text + " is missing ,\n");
    }
    if (ctx.END_FUNCTION().toString() !== ")") {
      str = str.concat("Repetition " + ctx.text + " is missing )\n");
    }
    return str;
  }

  // Visits repeat's nodes (objects),
  // and appends to the string.
  static visitOverlay(ctx: OverlayContext): string {
    let str = "";
    ctx.object().forEach((o: ObjectContext) => {
      str = str.concat(this.visitObject(o));
    });
    return str;
  }

  // Shape constructors
  // Visits shape constructor's nodes (circle, line,
  // petal, cardioid, and limacon), appends to the string.
  static visitShapeConstructor(ctx: ShapeConstructorContext): string {
    let str = "";
    if (ctx.circle() != undefined) {
      str = str.concat(this.visitCircle(ctx.circle()!));
    } else if (ctx.line() != undefined) {
      str = str.concat(this.visitLine(ctx.line()!));
    } else if (ctx.petal() != undefined) {
      str = str.concat(this.visitPetal(ctx.petal()!));
    } else if (ctx.cardioid() != undefined) {
      str = str.concat(this.visitCardioid(ctx.cardioid()!));
    } else if (ctx.limacon() != undefined) {
      str = str.concat(this.visitLimacon(ctx.limacon()!));
    }
    return str;
  }

  // Visits circle's nodes (arithexp, color, and shapemode),
  // appends to the string.
  static visitCircle(ctx: CircleContext): string {
    let str = "";
    str = str.concat(this.visitArithexp(ctx.arithexp()));
    str = str.concat(this.getColor(ctx.color()));
    str = str.concat(this.getShapeMode(ctx.shapeMode()));
    return str;
  }

  // Visits line's nodes (arithexp, and color),
  // appends to the string.
  static visitLine(ctx: LineContext): string {
    let str = "";
    str = str.concat(this.visitArithexp(ctx.arithexp()));
    str = str.concat(this.getColor(ctx.color()));
    return str;
  }

  // Visits petal's nodes (arithexp, color, and shapemode),
  // appends to the string.
  static visitPetal(ctx: PetalContext): string {
    let str = "";
    str = str.concat(this.visitArithexp(ctx.arithexp()));
    str = str.concat(this.getColor(ctx.color()));
    str = str.concat(this.getShapeMode(ctx.shapeMode()));
    return str;
  }

  // Visits cardioid's nodes (arithexp, color, and shapemode),
  // appends to the string.
  static visitCardioid(ctx: CardioidContext): string {
    let str = "";
    str = str.concat(this.visitArithexp(ctx.arithexp()));
    str = str.concat(this.getColor(ctx.color()));
    str = str.concat(this.getShapeMode(ctx.shapeMode()));
    return str;
  }

  // Visits limacon's nodes (arithexp, color, and shapemode),
  // appends to the string.
  static visitLimacon(ctx: LimaconContext): string {
    let str = "";
    str = str.concat(this.visitArithexp(ctx.arithexp()));
    str = str.concat(this.getColor(ctx.color()));
    str = str.concat(this.getShapeMode(ctx.shapeMode()));
    return str;
  }

  // Control Flows
  // Visits forloop's nodes (arithexps, variable, and statements),
  // appends to the string. Checks if it's missing the constants
  // "in range(", "," ")" "{" or "{"
  static visitForloop(ctx: ForloopContext): string {
    let str = "";
    str = str.concat(this.getVariable(ctx.variable()));
    str = str.concat(this.visitArithexp(ctx.arithexp(0)));
    str = str.concat(this.visitArithexp(ctx.arithexp(1)));
    str = str.concat(this.getStatements(ctx.statements()));
    if (ctx.RANGE().toString() !== "in range(") {
      str = str.concat("Incorrect syntax for forloop's range.\n");
    }
    if (ctx.COMMA().toString() !== ",") {
      str = str.concat("Forloop " + ctx.text + " is missing ,\n");
    }
    if (ctx.END_FUNCTION().toString() !== ")") {
      str = str.concat("Forloop " + ctx.text + " is missing )\n");
    }
    if (ctx.START_BODY().toString() !== "{") {
      str = str.concat("Forloop " + ctx.text + " is missing {\n");
    }
    if (ctx.END_BODY().toString() !== "}") {
      str = str.concat("Forloop " + ctx.text + " is missing }\n");
    }
    return str;
  }

  // Visits ifblock's nodes (compexp, then statements,
  // and if there are any else statements), appends to
  // the string. Checks if it's missing the constants
  // {} ()
  static visitIfblock(ctx: IfblockContext): string {
    let str = "";
    str = str.concat(this.visitCompexp(ctx.compexp()));
    ctx._thenblock.statement().forEach((s: StatementContext) => {
      str = str.concat(this.visitStatement(s));
    });
    if (ctx.ELSE() != undefined) {
      ctx._elseblock.statement().forEach((s: StatementContext) => {
        str = str.concat(this.visitStatement(s));
      });
      if (ctx.START_BODY(1).toString() !== "{") {
        str = str.concat("Else statements " + ctx.text + " is missing {\n");
      }
      if (ctx.END_BODY(1).toString() !== "}") {
        str = str.concat("Else statements " + ctx.text + " is missing }\n");
      }
    }
    if (ctx.END_FUNCTION().toString() !== ")") {
      str = str.concat("If statements " + ctx.text + " is missing )\n");
    }
    if (ctx.START_BODY(0).toString() !== "{") {
      str = str.concat("If statements " + ctx.text + "is missing {\n");
    }
    if (ctx.END_BODY(0).toString() !== "}") {
      str = str.concat("If statements " + ctx.text + " is missing }\n");
    }
    return str;
  }

  // Expressions
  // Visits arithmetic expression's nodes (vairable, num,
  // if it has any arithmetic operations and other arithexps),
  // appends to the string. Checks if it's a valid number if num
  static visitArithexp(ctx: ArithexpContext): string {
    let str = "";
    if (ctx.variable() != undefined) {
      str = str.concat(this.getVariable(ctx.variable()!));
    } else if (ctx.NUM() != undefined) {
      try {
        parseInt(ctx.NUM()!.toString(), 10);
      } catch (error) {
        str = str.concat(ctx.NUM()!.toString() + " is not a number.\n");
      }
    }
    if (ctx.aop() != undefined && ctx.arithexp() != undefined) {
      str = str.concat(this.getAop(ctx.aop()!));
      str = str.concat(this.visitArithexp(ctx.arithexp()!));
    }
    return str;
  }

  // Visits comparison expression's nodes (arithexps and comparison
  // operation), appends to the string. Checks if it's a valid
  // number if num
  static visitCompexp(ctx: CompexpContext): string {
    let str = "";
    str = str.concat(this.visitArithexp(ctx.arithexp(0)));
    str = str.concat(this.getCop(ctx.cop()));
    str = str.concat(this.visitArithexp(ctx.arithexp(1)));
    return str;
  }

  // Constants
  // They all return empty string, no check needed
  static getColor(_ctx: ColorContext): string {
    return "";
  }

  static getShapeMode(_ctx: ShapeModeContext): string {
    return "";
  }

  static getVariable(_ctx: VariableContext): string {
    return "";
  }

  static getAop(_ctx: AopContext): string {
    return "";
  }

  static getCop(_ctx: CopContext): string {
    return "";
  }
}
