import { Element } from "./Element";
import { Rotation, Repetition, Overlay } from "./Transformations";
import { Shape } from "./Shapes";
import { ArticleVariable } from "./Variables";
import { Visitor } from "./Visitor";
import { ArithExp } from "./Expressions";
import { ForLoop, IfBlock } from "./ControlBlocks";

// Program:     list of Statements and Draw
// Statement:   Assignment | ForLoop | IfBlock
// Draw:        Article

// Assignment:  ArticleAssignment | NumAssignment => Variable declaration and initialization
// Article:     Rotation | Repetition | Overlay | Shape | ArticleVariable => Shape related objects

export type Article = Rotation | Repetition | Overlay | Shape | ArticleVariable;

export type Statement = Assignment | IfBlock | ForLoop;

export class Program implements Element {
  statements: Statement[];
  draw: Draw;

  constructor(statements: Statement[], draw: Draw) {
    this.statements = statements;
    this.draw = draw;
  }

  accept<JType, NumType, CType>(
    visitor: Visitor<JType, NumType, CType>,
  ): JType {
    return visitor.visitProgram(this);
  }
}

export type Assignment = ArticleAssignment | NumAssignment;

export class ArticleAssignment implements Element {
  variable: string;
  value: Article;

  constructor(variable: string, shape: Article) {
    this.variable = variable;
    this.value = shape;
  }

  accept<JType, NumType, CType>(visitor: Visitor<JType, NumType, CType>): void {
    visitor.visitArticleAssignment(this);
  }
}

export class NumAssignment implements Element {
  variable: string;
  value: ArithExp;

  constructor(variable: string, value: ArithExp) {
    this.variable = variable;
    this.value = value;
  }

  accept<JType, NumType, CType>(visitor: Visitor<JType, NumType, CType>): void {
    visitor.visitNumAssignment(this);
  }
}

export class Draw implements Element {
  draw: Article;

  constructor(draw: Article) {
    this.draw = draw;
  }

  accept<JType, NumType, CType>(
    visitor: Visitor<JType, NumType, CType>,
  ): JType {
    return visitor.visitDraw(this);
  }
}
