import { Rotation, Repetition, Overlay } from "../ast/Transformations";
import {
  Program,
  ArticleAssignment,
  NumAssignment,
  Draw,
} from "../ast/Program";
import { Circle, Line, Petal, Cardioid, Limacon } from "../ast/Shapes";
import { ArticleVariable, NumberVariable } from "../ast/Variables";
import { Visitor } from "../ast/Visitor";
import { CompExp, ArithExp } from "../ast/Expressions";
import { ForLoop, IfBlock } from "../ast/ControlBlocks";
import {
  DuplicateVariableError,
  UndefinedVariableError,
  TypeError,
} from "./errors";

export class Validator implements Visitor<void, void, void> {
  private varTypeMappings: { [name: string]: "ARTICLE" | "NUMBER" } = {};
  // set of variables that cannot be reassigned ie loop variables
  private lockedVars: Set<string> = new Set();

  // Traverses the program
  visitProgram(program: Program): void {
    program.statements.forEach((s) => s.accept(this));
    program.draw.accept(this);
  }

  // Traverses aritcle assignment throws a DuplicateVariableError
  // if it is trying to reassign a locked variable
  visitArticleAssignment(assignment: ArticleAssignment): void {
    const isLocked = this.lockedVars.has(assignment.variable);
    if (isLocked) {
      throw new DuplicateVariableError(assignment.variable);
    }
    this.varTypeMappings[assignment.variable] = "ARTICLE";
    assignment.value.accept(this);
  }

  // Traverses number assignment throws a DuplicateVariableError
  // if it is trying to reassign a locked variable
  visitNumAssignment(assignment: NumAssignment): void {
    const isLocked = this.lockedVars.has(assignment.variable);
    if (isLocked) {
      throw new DuplicateVariableError(assignment.variable);
    }
    this.varTypeMappings[assignment.variable] = "NUMBER";
    assignment.value.accept(this);
  }

  // Traverses draw
  visitDraw(draw: Draw): void {
    draw.draw.accept(this);
  }

  // Traverses forloop throws a DuplicateVariableError if
  // the loop counter is already defined. Loop counter only
  // exists within the loop
  visitForLoop(loop: ForLoop): void {
    const isCounterDefined = this.varTypeMappings[loop.counter] !== undefined;
    if (isCounterDefined) {
      throw new DuplicateVariableError(loop.counter);
    }

    this.lockedVars.add(loop.counter);
    this.varTypeMappings[loop.counter] = "NUMBER";

    loop.rangeStart.accept(this);
    loop.rangeEnd.accept(this);
    loop.statements.forEach((s) => s.accept(this));

    // undefine counter variable when loop ends
    this.lockedVars.delete(loop.counter);
    delete this.varTypeMappings[loop.counter];
  }

  // Traverses ifblock
  visitIfBlock(block: IfBlock): void {
    block.condition.accept(this);

    block.ifStatements.forEach((statement) => {
      statement.accept(this);
    });
    block.elseStatements?.forEach((statement) => {
      statement.accept(this);
    });
  }

  // Traverses ArithExp
  visitArithExp(expression: ArithExp): void {
    expression.value.accept(this);
    expression.otherExp?.accept(this);
  }

  // Traverses CompExp
  visitCompExp(expression: CompExp): void {
    expression.leftArithExp.accept(this);
    expression.rightArithExp.accept(this);
  }

  // Traverses Rotation
  visitRotation(rotation: Rotation): void {
    rotation.shape.accept(this);
    rotation.angle.accept(this);
  }

  // Traverses Repetition
  visitRepetition(repetition: Repetition): void {
    repetition.shape.accept(this);
    repetition.repetitions.accept(this);
    repetition.offset.accept(this);
  }

  // Traverses Overlay
  visitOverlay(overlay: Overlay): void {
    overlay.shapes.forEach((s) => s.accept(this));
  }

  // Throws a UndefinedVariableError if the aritcle is not previously
  // defined, throws a TypeError if trying to use a number variable
  // for an article
  visitArticleVariable(variable: ArticleVariable): void {
    const isDefined = this.varTypeMappings[variable.name] !== undefined;
    if (!isDefined) {
      throw new UndefinedVariableError(variable.name);
    }

    const isArticle = this.varTypeMappings[variable.name] === "ARTICLE";
    if (!isArticle) {
      throw new TypeError(variable.name, "shape");
    }
  }

  // Throws a UndefinedVariableError if the number is not previously
  // defined, throws a TypeError if trying to use an article variable
  // for a number
  visitNumberVariable(variable: NumberVariable): void {
    const isDefined = this.varTypeMappings[variable.name] !== undefined;
    if (!isDefined) {
      throw new UndefinedVariableError(variable.name);
    }

    const isNumber = this.varTypeMappings[variable.name] === "NUMBER";
    if (!isNumber) {
      throw new TypeError(variable.name, "number");
    }
  }

  visitNum(): void {
    return;
  }

  // Checks circle's radius
  visitCircle(circle: Circle): void {
    circle.radius.accept(this);
  }

  // Checks line's length
  visitLine(line: Line): void {
    line.size.accept(this);
  }

  // Checks petal's size
  visitPetal(petal: Petal): void {
    petal.size.accept(this);
  }

  // Checks cardioid's size
  visitCardioid(cardioid: Cardioid): void {
    cardioid.size.accept(this);
  }

  // Checks limacon's size
  visitLimacon(limacon: Limacon): void {
    limacon.size.accept(this);
  }
}
