import { Element } from "./Element";
import { EvaluatesToNumber } from "./EvaluatesToNumber";
import { Article } from "./Program";
import { Visitor } from "./Visitor";

// Rotation:    article (shape to be rotated), angle
// Repetition:  article (shape to be repeated), number
//              of repetitions, offset from the center
// Overlay:     list of articles to be overlayed

export class Rotation implements Element {
  shape: Article;
  angle: EvaluatesToNumber;

  constructor(shape: Article, angle: EvaluatesToNumber) {
    this.shape = shape;
    this.angle = angle;
  }

  accept<JType, NumType, CType>(
    visitor: Visitor<JType, NumType, CType>,
  ): JType {
    return visitor.visitRotation(this);
  }
}

export class Repetition implements Element {
  shape: Article;
  repetitions: EvaluatesToNumber;
  offset: EvaluatesToNumber;

  constructor(
    shape: Article,
    rep: EvaluatesToNumber,
    offset: EvaluatesToNumber,
  ) {
    this.shape = shape;
    this.repetitions = rep;
    this.offset = offset;
  }

  accept<JType, NumType, CType>(
    visitor: Visitor<JType, NumType, CType>,
  ): JType {
    return visitor.visitRepetition(this);
  }
}

export class Overlay implements Element {
  shapes: Article[];

  constructor(shapes: Article[]) {
    this.shapes = shapes;
  }

  accept<JType, NumType, CType>(
    visitor: Visitor<JType, NumType, CType>,
  ): JType {
    return visitor.visitOverlay(this);
  }
}
