import { Element } from "./Element";
import { EvaluatesToNumber } from "./EvaluatesToNumber";
import { Visitor } from "./Visitor";

// A variable is either assigned to a shape (ie article)
// or a number

export type Variable = ArticleVariable | NumberVariable;

export class ArticleVariable implements Element {
  name: string;

  constructor(name: string) {
    this.name = name;
  }

  accept<JType, NumType, CType>(
    visitor: Visitor<JType, NumType, CType>,
  ): JType {
    return visitor.visitArticleVariable(this);
  }
}

export class NumberVariable implements EvaluatesToNumber {
  name: string;

  constructor(name: string) {
    this.name = name;
  }

  accept<JType, NumType, CType>(
    visitor: Visitor<JType, NumType, CType>,
  ): NumType {
    return visitor.visitNumberVariable(this);
  }
}
